import React, { useState } from 'react';
import Anchor from 'react-anchor-link-smooth-scroll';
import 'bootstrap/dist/css/bootstrap.min.css';
import './assets/css/fontawesome.css';
import './assets/css/templatemo-style.css';
//import './assets/css/owl.css';
//import './assets/css/lightbox.css';
//importing pictures
import FacePic from './assets/images/author-image.jpg';
import PicLeft from './assets/images/left-image.jpg';
import PicRight from './assets/images/right-image.jpg';

//import components
import { Button } from './components/Button';
import SignInGoogle from './components/SignIn/SignInGoogle';
import Email from './components/Email';
import MyWork from './components/MyWorkRButton';
//import { workData } from './components/WorkDataArray';
//import WorkList from './components/MyWorkList';

//import icons
import { FaLinkedinIn } from "react-icons/fa";
import { FaMailBulk } from "react-icons/fa";

import { TiCode } from "react-icons/ti";
import { FaAdobe } from "react-icons/fa";
import { FaDatabase } from "react-icons/fa";
import { FaVideo } from "react-icons/fa";

function Home() {

  const [hobbiesBtn, setHobbiesBtn] = useState(true);
  const [sportsBtn, setSportsBtn] = useState(true);

  const [userName, setUserName] = useState(null);
  const [userEmail, setUserEmail] = useState(null);

  return (
    <div>
      <div id="page-wraper">
        {/* Sidebar Menu */}
        <div className="responsive-nav">
          <i className="fa fa-bars" id="menu-toggle"></i>
          <div id="menu" className="menu">
            <i className="fa fa-times" id="menu-close"></i>
            <div className="container">
              <div className="image">
                <Anchor href="#page-wraper"><img src={FacePic} alt="" /></Anchor>
              </div>
              <div className="author-content">
                <h2 className="myname">József Sidó</h2>
                <span>Junior Web Developer</span>
              </div>
              <nav className="main-nav" role="navigation">
                <ul className="main-menu">
                  <li><Anchor href='#aboutme'>About Me</Anchor></li>
                  <li><Anchor href='#goodat'>What I'm good at</Anchor></li>
                  <li><Anchor href='#mywork'>My Work</Anchor></li>
                  <li><Anchor href='#contact'>Contact Me</Anchor></li>
                </ul>
              </nav>
              <div className="social-network">
                <ul className="soial-icons">
                  <li>
                    <a href="https://www.linkedin.com/in/j%C3%B3zsef-sid%C3%B3-b047bb17a/"><FaLinkedinIn /></a>
                  </li>
                  <li>
                    <a href="mailto:jozsef.sido@gmail.com"><FaMailBulk /></a>
                  </li>
                </ul>
              </div>
              <div className="copyright-text">
                <p>Designed in 2020 - 2023</p>
              </div>
            </div>
          </div>
        </div>

        <section className="section about-me" data-section="section1">
          <div className="container">
            <div className="section-heading">
              <h2 id="aboutme">About Me</h2>
              <div className="line-dec"></div>
              <span>
                <p>
                  I'm currently a barista for Pret A Manger and have been in this role since 2018.
                  I'm also a part time web developer for a property management company since 2021.
                </p>
                <p>
                  I learn fast and am eager to acquire new skills. I like working both individually and as a
                  team player. I can imagine myself as an experienced website developer working at a friendly
                  company along with other team members in helping each other's work.
                  I have high numeric, analytical and problem-solving skills. Also, I have great experience in
                  pursuing excel and photoshop-related projects which required advanced knowledge.
                </p>
                <p>
                  I started learning and developing in React in the last few years in order to build much better,
                  more responsive and more advanced websites.
                </p>
              </span>
            </div>
            <div className="left-image-post">
              <div className="row">
                <div className="col-md-6">
                  <div className="left-image">
                    <img src={PicLeft} alt="" />
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="right-text">
                    <h4>Hobbies and Interests</h4>
                    <p>
                      I'm a self-taught React Developer with a few years experience. In my sparetime I learn programming
                      and try to acquire different programming skills as this is not just work for me, it is my hobby too.
                    </p>
                    <p style={hobbiesBtn ? { display: 'none' } : {}}>
                      I love creating something new. I find it exciting to learn the latest techniques to be better and better.
                      I imagine myself as a lead developer in the future to deliver the best to the company I work for.
                    </p>
                    {Button(hobbiesBtn, setHobbiesBtn, hobbiesBtn ? 'more' : 'less')}
                  </div>
                </div>
              </div>
            </div>
            <div className="right-image-post">
              <div className="row">
                <div className="col-md-6">
                  <div className="left-text">
                    <h4>Sport and Social Activities</h4>
                    <p>
                      I love swimming, it can be extremely relaxing. Swimming offers many different strokes, so it doesn’t feel like
                      you’re stuck doing the same thing over and over again.
                    </p>
                    <p style={sportsBtn ? { display: 'none' } : {}}>
                      It’s a life skill which everyone should have. Not only is it good for your health, but you never know when
                      you’ll need your ability to swim.
                    </p>
                    {Button(sportsBtn, setSportsBtn, sportsBtn ? 'more' : 'less')}
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="right-image">
                    <img src={PicRight} alt="" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="section my-services" data-section="section2">
          <div className="container">
            <div className="section-heading">
              <h2 id="goodat">What I’m good at?</h2>
              <div className="line-dec"></div>
              <p>
                I'm always learning and challenging myself! Whether I'm learning a new programming language,
                trying out a new framework, or trying to solve a problem with a new algorithm, I'm always stretching
                my mind. I've learned so much by overcoming the challenges that come with coding. Not only does coding
                make me a better problem solver but it also teaches me patience, perseverance, and discipline. Coding
                pushes me outside of my comfort zone in the best way.
              </p>
              <p>
                However, my mindset completely shifted when I started approaching the problems and bugs I found while
                coding from a curious perspective. Instead of talking down on myself and saying things like why aren't
                I smart enough, why can't I figure this out, maybe this isn't for me, I decided to ask myself better
                questions. I found myself going to StackOverFlow, Google, forums and Udemy — and every single time,
                I would be able to solve that nasty bug or memory leak or whatever I thought was impossible!
                It's kind of like being a detective — going to multiple sources, collecting information,
                and putting the pieces together. The feeling when you finally figure it out is so rewarding!
              </p>
            </div>
            <div className="row">
              <div className="col-md-6">
                <div className="service-item">
                  <div className="big-icons"><TiCode /></div>
                  <h4>Web Development</h4>
                  <p>
                    HTML, CSS and React JS that I've been learning for the last several years.
                  </p>
                  <p>
                    HTML5
                    - CSS3, Sass
                    - Javascript
                    - React.js
                    - Node.js
                    - Express.js
                    - Socket.io
                    - SQL
                    - Knex
                    - SEO
                    - Bootstrap
                    - Github
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="service-item">
                  <div className="big-icons"><FaAdobe /></div>
                  <h4>Adobe Family</h4>
                  <p>
                    I'm good at Photoshop, Design marketing material (brochures, logo, company profile and business cards in Photoshop.)
                    Illustrator is essential to make vector-graphic designs. I am fully familiar of all of these.
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="service-item">
                  <div className="big-icons"><FaDatabase /></div>
                  <h4>PostrgeSQL - NoSQL</h4>
                  <p>
                    I've been using database with the help of PostgreSQL in my latest projects, and just getting familiarised with Firebase, too.
                  </p>
                </div>
              </div>
              <div className="col-md-6">
                <div className="service-item">
                  <div className="big-icons"><FaVideo /></div>
                  <h4>Video Editing</h4>
                  <p>
                    Premiere Pro is in my fingertips. Cutting, filtering out parts of the screen, making subtitles, color-correcting are so easy for me.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="section my-work" data-section="section3">
          <div className="container">
            <div className="section-heading">
              <h2 id="mywork">My Work</h2>
              <div className="line-dec"></div>
              <span>
                Some of my work below was made in the very early stages of learning React and some of them are
                new experiments to demonstrate my abilities.
              </span>
            </div>
            <div className="row">
              <div className="isotope-wrapper">
                <MyWork />
              </div>
            </div>
          </div>
        </section>

        <section className="section contact-me" data-section="section4">
          <div className="container">
            <div className="section-heading">
              <h2 id="contact">Contact Me</h2>
              <div className="line-dec"></div>
              <span>
                Email me at jozsef.sido@gmail.com or leave me a message using the form below.
                </span>
            </div>
            <SignInGoogle
              userName={userName}
              setUserName={setUserName}
              setUserEmail={setUserEmail}
            />
            <Email
              userName={userName}
              userEmail={userEmail}
            />
          </div>
        </section>
      </div>
    </div>
  );
}

export default Home;
