import React from 'react'
import '../assets/css/templatemo-style.css';

export default function Email({ userName, userEmail }) {
  return (
    <div className="contact-row">
      <div className="right-content">
        <div className="container">
          <form id="contact">
            <div className="row">
              <div className="col-md-6">
                <fieldset>
                  <input
                    name="name"
                    type="text"
                    className="form-control"
                    id="name"
                    placeholder="Name"
                    required=""
                    disabled={userName === null}
                  />
                </fieldset>
              </div>
              <div className="col-md-6">
                <fieldset>
                  <input
                    name="email"
                    type="text"
                    className="form-control"
                    id="email"
                    placeholder="Email"
                    required=""
                    disabled={userName === null}
                  />
                </fieldset>
              </div>
              <div className="col-md-12">
                <fieldset>
                  <input
                    name="subject"
                    type="text"
                    className="form-control"
                    id="subject"
                    placeholder="Subject"
                    required=""
                    disabled={userName === null}
                  />
                </fieldset>
              </div>
              <div className="col-md-12">
                <fieldset>
                  <textarea
                    name="message"
                    rows="6"
                    className="form-control"
                    id="message"
                    placeholder="Message"
                    required=""
                    disabled={userName === null}
                  ></textarea>
                </fieldset>
              </div>
              <div className="col-md-12">
                <fieldset>
                  <div id="form-submit" className="white-button" style={userName === null ? { opacity: '.2' } : {}}>
                    Send Message
                  </div>
                </fieldset>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}